import React, { useState } from 'react';
import { Button, createTheme, ThemeProvider } from "@mui/material";
import ProjectSlider from './ProjectSlider';
import { projectdata } from './projectdata';
import './styles/Projects.css';

const projectsTheme = createTheme({
  components: {
    MuiButton: {
      styleOverrides: {
        containedPrimary: {
          color: "white",
          backgroundColor: "#26a641",
          "&:hover": {
            backgroundColor: "#26a641",
            color: "white"
          }
        },
      }
    }
  }
})

export default function Projects() {
  const [ showProjects, setShowProjects ] = useState(true);

  return (
    <section id="projects">
      <div id="projects-container">
        <ThemeProvider theme={projectsTheme}>
        <div id="button-container">
          <Button variant="contained" onClick={() => setShowProjects(!showProjects)}>{ showProjects ? "Hide" : "Show" } Projects</Button>
        </div>
          <ProjectSlider projectdata={projectdata} showProjects={showProjects}/>
        </ThemeProvider>
      </div>
    </section>
  )
}